import React, { useState, useContext } from "react";
import { MDBContainer, MDBModal, MDBModalBody, MDBModalHeader } from "mdbreact";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { postLoginCliente } from "../../../services/authServiceCliente";
import AuthContext from "../context/authContext";
import Swal from "sweetalert2";

const ClientModalSession = ({
  setMostrarModalSession,
  mostrarModalSession,
  history,
}) => {
  const [formulario, setFormulario] = useState({
    correo: "",
    password: "",
  });

  const handleChange = (e) => {
    setFormulario({
      ...formulario,
      [e.target.name]: e.target.value,
    });
  };
  const { iniciarSesionContext } = useContext(AuthContext);
  const handleSubmit = (e) => {
    e.preventDefault();
    postLoginCliente(formulario).then((rpta) => {
      if (rpta.data.ok) {
        if (rpta.data.usu_tipo === "cliente") {
          //informar la contexto que hemos iniciado sesion
          iniciarSesionContext(rpta.data.token);
          history.push("/cliente/oferta");
          setMostrarModalSession(false);
        } else {
          Swal.fire({
            text: "Usted no est registrado como Cliente",
            icon: "error",
            timer: 2000,
          });
        }
      } else {
        Swal.fire({
          text: "Email o contrasea es incorrecta",
          icon: "error",
          timer: 2000,
        });
      }
    });
  };

  return (
    <MDBContainer>
      <MDBModal
        isOpen={mostrarModalSession}
        toggle={() => setMostrarModalSession(false)}
      >
        <MDBModalHeader toggle={() => setMostrarModalSession(false)}>
          <i className="fas fa-key"></i> Iniciar Sesin / Regstrate
        </MDBModalHeader>
        <MDBModalBody>
          <div className="col-md-12">
            <form className="formulario" onSubmit={handleSubmit}>
              <div className="form-group">
                <label for="exampleInputEmail1">Usuario</label>
                <input
                  type="text"
                  className="form-control"
                  name="correo"
                  value={formulario.correo}
                  onChange={handleChange}
                  placeholder="Ejm: jramosn@uni.edu.pe"
                />
                <small id="emailHelp" className="form-text text-muted">
                  Nunca compartiremos su correo electrnico con nadie ms.
                </small>
              </div>
              <div className="form-group">
                <label for="exampleInputPassword1">Contrasea</label>
                <input
                  type="password"
                  className="form-control"
                  name="password"
                  value={formulario.password}
                  onChange={handleChange}
                  placeholder="Contrasea"
                />
              </div>
              <button type="submit" className="btn btn__mostaza">
                Acceso
              </button>
              <Link
                className="btn btn__azul ml-2"
                to="/cliente/registro"
                role="button"
                onClick={() => setMostrarModalSession(false)}
              >
                Regstrate
              </Link>
            </form>
          </div>
        </MDBModalBody>
      </MDBModal>
    </MDBContainer>
  );
};

export default withRouter(ClientModalSession);
